import React,{Component, useState} from 'react'
import {Link} from 'react-router-dom'
import EcommerceContext from '../Context/EcommerceContext'

function Menu(props){
  const [colorButton, setColorButton] = useState({color: "#0088a9"})
  const [iconButton, setIconButton] = useState("fas fa-user")

  const logoutButton = () => {
    setColorButton({color:"rgb(182, 66, 66)"})
    setIconButton("fas fa-times-circle")
  }

  const userButton = () => {
    setColorButton({color: "#0088a9"})
    setIconButton("fas fa-user")
  }

  return(
    <EcommerceContext.Consumer>
      {
        context=>
        <div>
          <header className="header">
          <li className="nav_logo">E-CoMMERCE</li>
            <nav>
              <ul className="nav_links">
                {
                  context.userLogin &&
                  <>
                  <Link to="/"><li>Home</li></Link>
                  <li onClick={()=>context.logoutUser(false)} onMouseOver={logoutButton} onMouseOut={userButton} style={colorButton} className="username-t" data-title="log out">
                    <i className={iconButton}></i>
                    | {context.userInfo.name}
                  </li>
                  </>
                }
                {
                  !context.userLogin &&
                  <>
                  <Link to="/e"><li>Home</li></Link>
                  <Link to="/e/login"><li>Log In</li></Link>
                  <Link to="/e/signup"><li className='signup-t'>Sign Up</li></Link>
                  </>
                }
              </ul>
            </nav>
          </header>
          <header className="header_alt">
            <li className="nav_logo">E-CoMMERCE</li>
            <nav>
              <ul className="nav_links">
                <Link to="/"><li>Home</li></Link>
                <Link to="/login"><li>Log in</li></Link>
                <Link to="/signup"><li className='signup-t'>Sign up</li></Link>
              </ul>
            </nav>
          </header>
        </div>
      }
    </EcommerceContext.Consumer>
  )
}


export default Menu; 