import React,{Component, useEffect, useState} from 'react'
import ProductDetail from '../Components/ProductDetail'
import firebase from '../Config/Firebase'
import IButton from '../Components/IButton'
import { Link } from 'react-router-dom'
import AddItem from '../Components/AddItem'

function Details(props) {
  const id = props.match.params.id
  const [product, setProduct] = useState({})
  const [loading, setLoading] = useState(true)
  const [buttonText, setButtonText] = useState("Save changes")
  const [buttonIcon, setButtonIcon] = useState("fas fa-save")
  const [buttonColor, setButtonColor] = useState({backgroundColor:"#0088a9"})
  const [delText, setDelText] = useState("delete item")
  const [delIcon, setDelIcon] = useState("fas fa-minus-circle")
  const [delColor, setDelColor] = useState({backgroundColor:"#0088a9"})
  const [form, setForm] = useState({name:'', price:'', description:'', image:'', stock:''})
  const [edVisibility, setEdVisibility] = useState({opacity:0})
  const [edHidden, setEdHidden] = useState(true)
  const [adVisibility, setAdVisibility] = useState({opacity:0})
  const [adHidden, setAdHidden] = useState(true)
  const [deVisibility, setDeVisibility] = useState({opacity:0})
  const [deHidden, setDeHidden] = useState(true)

  const success = () => {
    setButtonText("Success")
    setButtonIcon("fas fa-thumbs-up")
    setButtonColor({backgroundColor:"green"})
  }

  const error = () => {
    setButtonIcon("fas fa-thumbs-down")
    setButtonColor({backgroundColor:"rgb(136, 0, 0)"})
    setButtonText("Error")
  }

  const process = () => {
    setButtonIcon("fas fa-arrow-circle-right")
    setButtonColor({backgroundColor:"#0088a9"})
    setButtonText("Add")
    setForm({name:'', price:'', description:'', image:'', stock:''})
  }

  useEffect(
    ()=>{
      firebase.db.doc('products/'+id)
      .get()
      .then(doc=>{
        setProduct(doc)
        setLoading(false)
        setForm({
          name:doc.data().name, 
          description:doc.data().description, 
          price:doc.data().price, 
          image:doc.data().image,
          stock:doc.data().stock, })
      })
    },
    []
  )

  const handleChange = (e) => {
    const name = e.target.name
    const value = e.target.value
    console.log(name, value)
    setForm({...form,[name]:value})
  }

  const handleSumbit = (e) => {
    e.preventDefault()
    firebase.db.doc("products/"+id)
    .set({
      name:form.name,
      description:form.description,
      price:form.price,
      image:form.image,
      stock:form.stock
    },{merge:true})

    .then(doc=>{
      console.log("Doc", doc)
      success()
    })

    .catch(error=>{
      console.log("Error", error)
      error()
      process()
    })
  }

  const handleDelete = (e) => {
    e.preventDefault()
    firebase.db.doc("products/"+id)
    .delete()
    .then(doc=>{
      console.log("Doc", doc)
      setDelColor({backgroundColor:"green"})
      setDelIcon("fas fa-thumbs-up")
      setDelText("success")
    })
    .catch(error=>{
      console.log("Error", error)
      setDelIcon("fas fa-thumbs-down")
      setDelColor({backgroundColor:"rgb(136, 0, 0)"})
      setDelText("Error")  
    })
  }

  const showDelCard = () => {
    if (deHidden) {
      setDeVisibility({opacity:1, zIndex:1})
      setDeHidden(false)
      setEdVisibility({opacity:0, zIndex:0})
      setEdHidden(true)
      setAdVisibility({opacity:0, zIndex:0})
      setAdHidden(true)
    } else {
      setDeVisibility({opacity:0, zIndex:0})
      setDeHidden(true)
    }
  }

  const showEditCard = () => {
    if (edHidden) {
      setEdVisibility({opacity:1, zIndex:1})
      setEdHidden(false)
      setAdVisibility({opacity:0, zIndex:0})
      setAdHidden(true)
      setDeVisibility({opacity:0, zIndex:0})
      setDeHidden(true)
    } else {
      setEdVisibility({opacity:0, zIndex:0})
      setEdHidden(true)
    }
  }

  const showAddCard = () => {
    if (adHidden) {
      setAdVisibility({opacity:1, zIndex:1})
      setAdHidden(false)
      setEdVisibility({opacity:0, zIndex:0})
      setEdHidden(true)
      setDeVisibility({opacity:0, zIndex:0})
      setDeHidden(true)
    } else {
      setAdVisibility({opacity:0, zIndex:0})
      setAdHidden(true)
    }
  }
  

  if (loading) {
    return(
      <p className="loader">cargando...</p>
    )
    
  }
  else {
    return(
      <div className="details">
        <p className="prod-detail_title">Product detail</p>
        <ProductDetail data={product.data()} id={product.id} />

        <div className="tool_btn_card">
          <IButton text="Add" icon="fas fa-plus-circle" click={showAddCard}/>
          <IButton text="Edit" icon="fas fa-edit" click={showEditCard} />
          <IButton text="Delete" icon="fas fa-minus-circle" click={showDelCard}/>
        </div>

        <div className="tools_card">

          <div className="tool_card" style={edVisibility}>
            <div className="tool">
              <form onSubmit={handleSumbit} className="tool_form">
                <input type="text" name="image" value={form.image} onChange={handleChange} className="tool_input" placeholder="Image (Enter the URL)"></input>
                <input type="text" name="name" value={form.name} onChange={handleChange} className="tool_input" placeholder="Name"></input>
                <input type="text" name="description" value={form.description} onChange={handleChange} className="tool_input" placeholder="Description"></input>
                <input type="number" min="1" name="price" value={form.price} onChange={handleChange} className="tool_input" placeholder="Price"></input>
                <input type="number" min="1" name="stock" value={form.stock} onChange={handleChange} className="tool_input" placeholder="Stock"></input>
                <IButton icon={buttonIcon} text={buttonText} type={"sumbit"} styles={buttonColor} />
              </form>
            </div>
          </div>

          <AddItem styles={adVisibility} />

          <div className="tool_card" style={deVisibility}>
            <div className="tool">
              <form onSubmit={handleDelete} className="tool_form">
                <p className="tool_delete">are you sure want to delete item?</p>
                <IButton text={delText} icon={delIcon} styles={delColor} type={"sumbit"} />
              </form>  
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Details; 