import React,{Component} from 'react'

function IButton(props){
  const {icon, text, type, styles, click, classn} = props

  return(
    <button className={classn || "tool_btn"} type={type} style={styles} onClick={click}>
      <i className={icon}></i>
      <p className="tool_txt">{"| "+text}</p>
    </button>
  )
}

export default IButton; 