import React,{useState, useContext} from 'react'
import {Link} from 'react-router-dom'
import firebase from '../Config/Firebase'
import IButton from '../Components/IButton'
import {useHistory} from 'react-router-dom'
import EcommerceContext from '../Context/EcommerceContext'

function Login (){

  const context = useContext(EcommerceContext)
  const [form, setForm] = useState({email:'', pass:''})
  const [buttonText, setButtonText] = useState("Continue")
  const [buttonIcon, setButtonIcon] = useState("fas fa-arrow-circle-right")
  const [buttonColor, setButtonColor] = useState({backgroundColor:"#0088a9"})
  const history = useHistory()

  const successLogin = () => {
    setButtonText("Success")
    setButtonIcon("fas fa-thumbs-up")
    setButtonColor({backgroundColor:"green"})
  }

  const errorLogin = () => {
    setButtonIcon("fas fa-thumbs-down")
    setButtonColor({backgroundColor:"rgb(136, 0, 0)"})
    setButtonText("Error")
    setTimeout(processLogin, 3000)
  }

  const processLogin = () => {
    setButtonIcon("fas fa-arrow-circle-right")
    setButtonColor({backgroundColor:"#0088a9"})
    setButtonText("Continue")
    setForm({
      email:"",
      pass:""
    })
  }

  const handleSumbit = (e) => {
    e.preventDefault()
    firebase.auth.signInWithEmailAndPassword(form.email, form.pass)
    .then(data=>{
      successLogin()

      firebase.db.collection("users")
      .where("userId","==",data.user.uid)
      .get()
      .then(querySnapshot=>{
        context.loginUser(querySnapshot.docs[0].data())
      })

      console.log('Login ok', data)
    })
    .catch(error=>{
      errorLogin()
      console.log('Error', error)
    })
  }

  const handleChange = (e) => {
    const name = e.target.name
    const value = e.target.value
    console.log(name, value)
    setForm({...form,[name]:value})
  }

  return(
    <div className="login">
      <div className='log_card'>
        <p className="log_title">Log in</p>

        <form className="log_form" onSubmit={handleSumbit}>

          <div className="log_field">
            <input type='email' value={form.email} onChange={handleChange} placeholder="Email" name="email" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='password' value={form.pass} onChange={handleChange} placeholder="Password" name="pass" required></input>
            <div className="underline"></div>
          </div>

          <p className="log_pass">Forgot password?</p>
          <IButton icon={buttonIcon} text={buttonText} type={"sumbit"} styles={buttonColor} classn={"log_btn"} />
          <div className="log_signup">New to e-commerce? <Link to="/e/signup">Sign up</Link></div>
        </form>
      </div>
    </div>
  )
}


export default Login; 