import React,{Component, useEffect, useState} from 'react'
import IButton from './IButton'
import firebase from '../Config/Firebase'

function AddItem(props){
  const [form, setForm] = useState({name:'', price:'', description:'', image:'', stock:''})
  const [buttonText, setButtonText] = useState("Add item")
  const [buttonIcon, setButtonIcon] = useState("fas fa-plus-circle")
  const [buttonColor, setButtonColor] = useState({backgroundColor:"#0088a9"})

  const successAdd = () => {
    setButtonText("Success")
    setButtonIcon("fas fa-thumbs-up")
    setButtonColor({backgroundColor:"green"})
  }

  const errorAdd = () => {
    setButtonIcon("fas fa-thumbs-down")
    setButtonColor({backgroundColor:"rgb(136, 0, 0)"})
    setButtonText("Error")
    setTimeout(processAdd, 3000)
  }

  const processAdd = () => {
    setButtonIcon("fas fa-arrow-circle-right")
    setButtonColor({backgroundColor:"#0088a9"})
    setButtonText("Add")
    setForm({name:'', price:'', description:'', image:'', stock:''})
  }
  
  const handleChange = (e) => {
    const name = e.target.name
    const value = e.target.value
    console.log(name, value)
    setForm({...form,[name]:value})
  }

  const handleSumbit = (e) => {
    e.preventDefault()
    firebase.db.collection("products")
    .add({
      name:form.name,
      description:form.description,
      price:form.price,
      image:form.image,
      stock:form.stock,
      sales:0
    },{merge:true})

    .then(doc=>{
      console.log("Doc", doc)
      successAdd()
    })

    .catch(error=>{
      console.log("Error", error)
      errorAdd()
    })
  }

  return(
    <div className="tool_card" style={props.styles}>
      <div className="tool">
        <form onSubmit={handleSumbit} className="tool_form">
          <input type="text" name="image" value={form.image} onChange={handleChange} className="tool_input" placeholder="Image (Enter the URL)"></input>
          <input type="text" name="name" value={form.name} onChange={handleChange} className="tool_input" placeholder="Name"></input>
          <input type="text" name="description" value={form.description} onChange={handleChange} className="tool_input" placeholder="Description"></input>
          <input type="number" min="1" name="price" value={form.price} onChange={handleChange} className="tool_input" placeholder="Price"></input>
          <input type="number" min="1" name="stock" value={form.stock} onChange={handleChange} className="tool_input" placeholder="Stock"></input>
          <IButton icon={buttonIcon} text={buttonText} type={"sumbit"} styles={buttonColor} />
        </form>
      </div>
    </div>
  )
}

export default AddItem; 