import React,{Component, useState} from 'react'
import { Link } from 'react-router-dom'
import EcommerceContext from '../Context/EcommerceContext'

function Product(props){
  const {data} = props
  const id = props.id
  const {price,name,image,stock} = props.data
  const [buttonText, setButtonText] = useState("Purchase")
  const [buttonColor, setButtonColor] = useState({backgroundColor:"#0088a9"})
  const forbiddenBuy = {cursor:"no-drop", backgroundColor:"#c8e0e7"}

  const updateButton = () => {
    if (stock < 1) {
      setButtonText("No stock")
      setButtonColor({backgroundColor:"rgb(136, 0, 0)"})

    }
    else {
      setButtonText("Purchased")
      setButtonColor({backgroundColor:"green"})
    }
  }

  return( 
    <EcommerceContext.Consumer>
      {
        context=>
        <div className="prod_card">
          <div className="prod_img">
            <img src={image}></img>
          </div>
          
          <div className="prod_text">
            <p className="prod_price">US$ {price}</p>
            <p className="prod_sku">{id}</p>
            <p className="prod_name">{name}</p>
            <Link to={'/e/product/'+id}>
              <button className="prod_button">See details</button>
            </Link>
            {
              context.userLogin &&
              <button className="prod_button" onClick={updateButton} style={buttonColor}>{buttonText}</button>
            }
            {
              !context.userLogin &&
              <button className="prod_button" style={forbiddenBuy} data-title="You must Log In to buy">{buttonText}</button>
            }
            
          </div>
        </div>
      }
    </EcommerceContext.Consumer>
  )
}


export default Product; 