import React,{Component, useEffect, useState} from 'react'
import Product from '../Components/Product'
import firebase from '../Config/Firebase'
import IButton from '../Components/IButton'
import AddItem from '../Components/AddItem'
import Loader from '../Components/Loader'

function Home(props) {
  const [products, setProducts] = useState([])
  const [loading, setLoading] = useState(true)
  const [visibility, setVisibility] = useState({opacity:0})
  const [hidden, setHidden] = useState(true)

  useEffect(
    ()=>{
      firebase.db.collection('products')
      .get()
      .then(querySnapshot=>{
        console.log(querySnapshot.docs)
        setProducts(querySnapshot.docs)
        setLoading(false)
      })
    },
    []
  )

  const showCard = () => {
    if (hidden) {
      setVisibility({opacity:1})
      setHidden(false)
    } else {
      setVisibility({opacity:0})
      setHidden(true)
    }
  }
  
  if (loading) {
    return(
      <Loader />
    )
  } 
  
  else {
    return( 
      <div className="home">
        <p className="home_title">Products List</p>
        {products.map(product=><Product id={product.id} data={product.data()} />)}
        <div className="tool_btn_card">
         <IButton text="Add" icon="fas fa-plus-circle" click={showCard}/>
        </div>
        <div className="tools_card">
          <AddItem styles={visibility} />
        </div>
      </div>
    )
  } 

}

export default Home; 

