import React,{Component} from 'react'

function Loader(props){

  return(
    <div className="load_card">
      <p className="load_text">loading</p>
      <div className="load">
        <div className="load_move"></div>
        <div className="load_static"></div>
      </div>
    </div>
  )
}

export default Loader; 